:root {
  --font-body: "Rubik", sans-serif;
}

body {
  font-family: var(--font-body);
}

:root {
  --Blue: hsl(246, 80%, 60%);
  --Light-red-work: hsl(15, 100%, 70%);
  --Soft-blue-play: hsl(195, 74%, 62%);
  --Light-red-study: hsl(348, 100%, 68%);
  --Lime-green-exercise: hsl(145, 58%, 55%);
  --Violet-social: hsl(264, 64%, 52%);
  --Soft-orange-self-care: hsl(43, 84%, 65%);
  --Very-dark-blue: hsl(226, 43%, 10%);
  --Dark-blue: hsl(235, 46%, 20%);
  --Desaturated-blue: hsl(235, 45%, 61%);
  --Pale-Blue: hsl(236, 100%, 87%);
  --hover: hsl(235, 40%, 34%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--Very-dark-blue);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-size: 1rem;
  color: var(--Pale-Blue);
  margin: 0;
}

h2 {
  color: white;
  font-size: 1.125rem;
  margin: 0;
}

.container {
  padding: 5rem 1.375rem;
  max-width: 69.5rem;
  margin: 0 auto;
  display: grid;
  gap: 1.375rem;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
}
@media (min-width: 37.5em) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 56.25em) {
  .container {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 68.75em) {
  .container {
    grid-template-columns: repeat(4, 1fr);
  }
}

.first {
  background-color: var(--Dark-blue);
  border-radius: 0.75rem;
}
@media (min-width: 37.5em) {
  .first {
    grid-row: 1/3;
  }
}
.first__top {
  padding: 1.875rem 1.5625rem;
  display: flex;
  align-items: center;
  background-color: var(--Blue);
  border-radius: 0.75rem;
}
@media (min-width: 37.5em) {
  .first__top {
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 5.5rem;
  }
}
.first__top img {
  border: 0.1875rem solid white;
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 50%;
  margin-right: 1rem;
}
@media (min-width: 37.5em) {
  .first__top img {
    margin-bottom: 2.625rem;
  }
}
@media (min-width: 37.5em) {
  .first__top--title p {
    margin-bottom: 0.8125rem;
  }
}
.first__top--title h1 {
  font-size: 1.375rem;
  color: white;
  font-weight: 400;
}
@media (min-width: 37.5em) {
  .first__top--title h1 {
    font-size: 1.875rem;
    font-weight: 300;
  }
}
.first__bottom {
  display: flex;
  padding: 1.5625rem;
  justify-content: space-between;
}
@media (min-width: 32.5em) {
  .first__bottom {
    flex-direction: column;
  }
  .first__bottom button:not(:last-child) {
    margin-bottom: 1.375rem;
  }
}
.first__bottom button {
  font-weight: 400;
  color: var(--Pale-Blue);
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.125rem;
  margin: 0;
  text-align: left;
}
.first__bottom button:hover {
  color: white;
}
.first__bottom button.active {
  color: white;
}

.second__bg {
  border-radius: 0.75rem;
  padding-top: 2.1875rem;
  position: relative;
  overflow: hidden;
}
.second__bg::before {
  content: "";
  width: 4.8125rem;
  height: 4.8125rem;
  position: absolute;
  top: -0.625rem;
  right: 1.25rem;
}
.second__bg--work {
  background-color: var(--Light-red-work);
}
.second__bg--work::before {
  background: url(../images/icon-work.svg);
}
.second__bg--play {
  background-color: var(--Soft-blue-play);
}
.second__bg--play::before {
  background: url(../images/icon-play.svg);
}
.second__bg--study {
  background-color: var(--Light-red-study);
}
.second__bg--study::before {
  background: url(../images/icon-study.svg);
}
.second__bg--exercise {
  background-color: var(--Lime-green-exercise);
}
.second__bg--exercise::before {
  background: url(../images/icon-exercise.svg);
}
.second__bg--social {
  background-color: var(--Violet-social);
}
.second__bg--social::before {
  background: url(../images/icon-social.svg);
}
.second__bg--self-care {
  background-color: var(--Soft-orange-self-care);
}
.second__bg--self-care::before {
  background: url(../images/icon-self-care.svg);
}
.second__main {
  background-color: var(--Dark-blue);
  border-radius: 0.75rem;
  padding: 1.875rem 1.4375rem;
  position: relative;
  transition: all 0.3s ease;
}
.second__main:hover {
  cursor: pointer;
  background-color: var(--hover);
}
.second__main--top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.125rem;
}
@media (min-width: 37.5em) {
  .second__main--top {
    margin-bottom: 2.5rem;
  }
}
.second__main--top h2 {
  font-weight: 500;
}
.second__main--bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 37.5em) {
  .second__main--bottom {
    flex-direction: column;
    align-items: flex-start;
  }
}
.second__main--bottom h3 {
  font-size: 1.5625rem;
  font-weight: 300;
  color: white;
  display: inline-block;
}
@media (min-width: 37.5em) {
  .second__main--bottom h3 {
    font-size: 2.625rem;
    margin-bottom: 1.5rem;
  }
}